import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';
import { OperationalLocation } from '@biz-nest/commerce-client';
import {
  AppNameService, CurrencyService, EventbusService,
  ListComposeAdminPanel, MainBarTitleService,
  scrollToTop, TooltipComponent, WindowService
} from '@front-shared-lib';
import { navByBus } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { AdminDateFilterFieldComponent } from '../../misc/admin-date-filter-field/admin-date-filter-field.component';
import { AdminModalComponent } from '../../misc/admin-modal/admin-modal.component';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { AdminSearchFieldComponent } from '../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../misc/admin-top-bar/admin-top-bar.component';

@Component({
  selector: 'app-operational-location-list',
  standalone: true,
  imports: [
    RouterModule,
    AdminModalComponent,
    TooltipComponent,
    AdminTableComponent,
    AdminPageContentComponent,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent
  ],
  templateUrl: './operational-location-list.component.html',
  styleUrl: './operational-location-list.component.scss'
})
export class OperationalLocationListComponent
implements OnInit, OnDestroy {
  cleanup?: () => void;
  listCompose = new ListComposeAdminPanel<OperationalLocation>(
    OperationalLocation,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  actions = [
    {
      label: 'View',
      icon: 'visibility',
      event: (opLoc) => this.viewOperationalLocation(opLoc)
    },
    {
      label: 'Update',
      icon: 'edit',
      event: (opLoc) => this.updateOperationalLocation(opLoc)
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (opLoc) => this.listCompose.openDeleteOneModal(
        opLoc._id,
        'Delete Operational Location',
        'Are You Sure You Want To Delete This Operational Location?'
      )
    }
  ];

  constructor(
    public currencyService: CurrencyService,
    private router: Router,
    private eventbus: EventbusService,
    private windowService: WindowService,
    private toastr: ToastrService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.listCompose.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.mainBarTitle.title = 'Operational Locations - Manage Operational Locations';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Overview of operational locations for effective management and tracking.'
    });
    this.listCompose.getDataArr();
    scrollToTop(this.windowService);
  }


  viewOperationalLocation(opLoc: OperationalLocation) {
    this.navigateByBus(
      ['/operational-location/view/' + opLoc._id],
      'oneItem',
      opLoc
    );
  }

  updateOperationalLocation(opLoc: OperationalLocation) {
    this.navigateByBus(['/operational-location/update'], 'updateOperationalLocation', opLoc);
  }

  addOperationalLocation() {
    this.router.navigate(['/operational-location/add']);
  }

  ngOnDestroy() {
    this.cleanup?.();
    this.listCompose.cleanup?.();
  }
}

